    color: rgba(255, 255, 255, 0.95)

// Fades from name to "i build stuff"
@keyframes nameFade
  0%
    content: "JEREMY FIELDS"
    color: rgba(255, 255, 255, 0.95)
  45%
    content: "JEREMY FIELDS"
    color: rgba(255, 255, 255, 0.95)
