.name:before
  content: "I BUILD THINGS"
  color: rgba(255, 255, 255, 0.95)
  animation-iteration-count: 1
  animation-direction: forwards
  animation-duration: 10s
  animation-name: nameFade

.name
	color: rgba(255, 255, 255, 0.95)
	font-family: 'News Cycle', sans-serif
	font-size: 50px
	letter-spacing: 5px

//––––––––––––––––––––––––––
//			Stars Animation
//––––––––––––––––––––––––––
$starFieldWidth: 2560
$starFieldHeight: 2560
$starStartOffset: 600px

$smallStarSpeed: 100s
$medStarSpeed: 125s
$largeStarSpeed: 175s
$numSmallStars: 1700
$numMedStars: 700
$numLargeStars: 200
$width: 100%

@function create-stars($n)
  $stars: "#{random($starFieldWidth)}px #{random($starFieldHeight)}px #FFF"
  @for $i from 2 through $n
    $stars: "#{$stars} , #{random($starFieldWidth)}px #{random($starFieldHeight)}px #FFF"
  @return unquote($stars)

@mixin star-template($numStars, $starSize, $scrollSpeed)
  z-index: 10
  width: $starSize
  height: $starSize
  border-radius: 50%
  background: transparent
  box-shadow: create-stars($numStars)
  -webkit-box-shadow: create-stars($numStars)
  animation: animStar $scrollSpeed linear infinite
  -webkit-animation: animStar $scrollSpeed linear infinite
  &:after
    content: " "
    top: -$starStartOffset
    width: $starSize
    height: $starSize
    border-radius: 50%
    position: absolute
    background: transparent
    box-shadow: create-stars($numStars)
    -webkit-box-shadow: create-stars($numStars)

.stars
  @include star-template($numSmallStars, 1px, $smallStarSpeed)
.stars1
  @include star-template($numMedStars, 2px, $medStarSpeed)
.stars2
  @include star-template($numLargeStars, 3px, $largeStarSpeed)

@keyframes animStar
  from
    transform: translateY(0px)
    -webkit-transform: translateY(0px)
    -ms-transform: translateY(0px)
  to
    transform: translateY(-#{$starFieldHeight}px) translateX(-#{$starFieldWidth}px)
    -webkit-transform: translateY(-#{$starFieldHeight}px) translateX(-#{$starFieldWidth}px)
    -ms-transform: translateY(-#{$starFieldHeight}px) translateX(-#{$starFieldWidth}px)

</style>



